<template>
  <Wrap>
    <div class="test" v-if="start  && !testOver">
      <span class="time"
        >本题倒计时：<i>{{ time }}</i></span
      >
      <span class="time"
        >本题倒计时：<i>{{ time }}</i></span
      >
      <div class="result-dialog" v-if="currentOver">
        <img v-if="this.result == 1" src="../../assets/高兴.png" alt="" />
        <img v-else src="../../assets/伤心.png" alt="" />
        <div>
          {{
            this.result == 1
              ? "答对了"
              : this.result == 2
              ? "答错了"
              : "没时间了"
          }}
        </div>
      </div>
      <div class="question">
        <p>
          第{{ currentQuestionIndex + 1 }}题：{{ currentQuestion.question }}
        </p>
        <ul>
          <li
            v-for="(item, index) in currentQuestion.options"
            @click="checked(item.text)"
            :key="index"
          >
            <i v-if="item.isTrue && currentOver" class="el-icon-check"></i>
            <i
              v-if="item.text == checkedText && currentOver && !item.isTrue"
              class="el-icon-close"
            ></i>
            {{ item.text }}
          </li>
        </ul>
        <div v-if="currentOver" class="center-button">
          <el-button @click="next" size="small" type="success"
            >下一题</el-button
          >
        </div>
      </div>
    </div>
    <div v-if="!start" class="start-wrap" @click="start = true">
      <img src="../../assets/准备.webp" alt="" />
      <div>
        <el-button icon="el-icon-video-play" icon-size="30" type="success"
          >开始测试</el-button
        >
      </div>
    </div>
    <div v-if="testOver" class="start-wrap">
      <img src="../../assets/小樱.gif" alt="" />
      <div>哇 (o゜▽゜)o☆ ！ 不愧是你！你得了100分</div>
    </div>
  </Wrap>
</template>
<script>
import Wrap from "../../components/CoreWrap.vue";
export default {
  components: { Wrap },
  // mounted() {
  //   this.countDown();
  // },
  watch: {
    start(newValue) {
      if (newValue) {
        this.countDown();
      }
    },
  },
  data() {
    return {
      start: false,
      time: 10,
      questionArr: [
        {
          question: "请选择あ对应的片假名",
          options: [
            { text: "ナ", isTrue: false },
            { text: "メ", isTrue: false },
            { text: "チ", isTrue: false },
            { text: "ア", isTrue: true },
          ],
        },
        {
          question: "请选择あ对应的片假名",
          options: [
            { text: "ナ", isTrue: false },
            { text: "メ", isTrue: false },
            { text: "チ", isTrue: false },
            { text: "ア", isTrue: true },
          ],
        },
        {
          question: "请选择あ对应的片假名",
          options: [
            { text: "ナ", isTrue: false },
            { text: "メ", isTrue: false },
            { text: "チ", isTrue: false },
            { text: "ア", isTrue: true },
          ],
        },
        {
          question: "请选择あ对应的片假名",
          options: [
            { text: "ナ", isTrue: false },
            { text: "メ", isTrue: false },
            { text: "チ", isTrue: false },
            { text: "ア", isTrue: true },
          ],
        },
      ],
      currentOver: false,
      testOver: false,
      result: 0, //0 时间内未回答、1 回答对、2、回答错了/超时
      timer: null,
      checkedText: "",
      currentQuestionIndex: 0,
    };
  },
  computed: {
    currentQuestion() {
      console.log(this.questionArr[this.currentQuestionIndex], "???????");
      return this.questionArr[this.currentQuestionIndex];
    },
    currentRight() {
      return this.currentQuestion.options.find((item) => item.isTrue).text;
    },
  },
  methods: {
    init() {
      this.time = 10;
      this.currentOver = false;
      this.result = 0;
      this.timer = null;
      this.checkedText = "";
      this.countDown();
    },
    checked(text) {
      this.clearTimer();
      this.checkedText = text;
      if (text == this.currentRight) {
        this.result = 1;
      } else {
        this.result = 2;
      }
      this.currentOver = true;
    },
    countDown() {
      this.timer = setInterval(() => {
        this.time = this.time - 1;
        if (this.time <= 0) {
          clearInterval(this.timer);
          this.currentOver = true;
          this.result = 0;
        }
      }, 1000);
    },
    next() {
      if (this.currentQuestionIndex >= this.questionArr.length-1) {
        this.testOver = true;
        return;
      }
      this.currentQuestionIndex = this.currentQuestionIndex + 1;
      this.init();
    },
    clearTimer() {
      clearInterval(this.timer);
    },
  },
};
</script>
<style lang="scss" scoped>
.test {
  width: 100%;
  height: 500px;
  position: relative;
  .question {
    position: absolute;
    left: 50%;
    top: 30%;
    transform: translate(-50%, 0);
    // background-color: #f5f5f5;
    color: #333;
    margin-top: 30px;
    > p {
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 30px;
    }
    li {
      width: 100%;
      font-size: 16px;
      font-weight: 500;
      padding: 12px 0;
      // background-color: pink;
      text-align: center;
      position: relative;
      cursor: pointer;
    }
  }
}
.time {
  position: absolute;
  right: 0;
  top: 0;

  font-weight: 600;
  color: $self-common-fontcolor;
  > i {
    font-size: 36px;
  }
}
.el-icon-check,
.el-icon-close {
  // color:#DC8A8A;
  color: $self-common-fontcolor;
  font-weight: 600;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.el-icon-check {
  color: $self-common-fontcolor;
}
.el-icon-close {
  color: $self-checked-color;
}
.result-dialog {
  width: fit-content;
  padding: 10px 20px;
  border: 6px;
  border-radius: 6px;
  background-color: #fff;
  text-align: center;
  margin: 0 auto;
  position: absolute;
  left: 50%;
  top: 50px;
  transform: translateX(-50%);
  background-color: #7ab56c;
}
.start-wrap {
  padding: 0;
  text-align: center;
  background-color: $self-checked-color;
  height: 500px;
  color: #fff;
  > img {
    margin-top: 50px;
    border-radius: 10px;
    width: 200px;
    margin-bottom: 50px;
  }
  .el-button--success {
    background-color: #dd6161;
    border: none;
  }
}
</style>